// Error description
.text {
  margin-bottom: 25px;
}

// Home button
.button {
  padding: 5px;

  border: 0px solid transparent;
  border-radius: 4px;

  text-decoration: none;

  color: $clear-color;
  background-color: $main-btnContact-color;

  box-shadow: $main-shadow;

  opacity: 1;
  transition: opacity $transition-duration $timing-function;

  &:hover,
  &:focus {
    opacity: 0.7;

    transition: opacity $transition-duration $timing-function;
  }

  &:disabled {
    background-color: $inactive-color;
    cursor: auto;
  }
}

// Animation for image
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
